@import url(https://fonts.googleapis.com/css?family=Roboto:regular,500,700,900&display=swap);

html {
  width: 100%;
  height: 100%;
  font-family: 'Roboto', sans-serif;
}

h1 {
  margin: 0;
  padding: 0;
}

button {
  border: none; 
  outline: none;
  display: block;
  background: transparent;
  color: white;
}


body {
  background-color: black;
  color: white;
  width: 100%;
  height: 100vh;
  caret-color: transparent;
 
}



#root {
  min-width: 280px;
  width: 100%;
  height: 100vh;
}

/*Name */
.name {
  min-width: 150px;
  max-width: 400px;
  height: 5%;
  margin: .5% auto 5% auto ;

  display: flex;
  justify-content: center;
  align-items: center;
  
  font-family: 'Roboto', sans-serif;
  font-size: 38px;
  letter-spacing: 20px;
  font-weight: 500;
  background: linear-gradient(to right,#18454a, rgb(58, 141, 90), rgb(49, 120, 185));
  border-radius: 2px;
}

@media screen and (max-width: 960px) {
  .name {
    font-size: 32px;
  } 
}
@media screen and (max-width: 768px) {
  .name {
    margin-top: 0;
  }
}

/*Table.js*/
.table-cotainer {
  max-width: 600px; 
  height: 50%;

  margin: 0 auto;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media screen and (max-width: 480px) {
  .table-cotainer {
    margin: 0 1% 0 1%;
  }
}

div[class*=row-container] {
  width: 100%;
  height: 15.833%;

  display: flex;
  justify-content: space-around; 
}

div[class*=row-container] > div {
  height: 100%;
  width: 19%; 
}

.table-cotainer div div {
  display: flex;
  justify-content: center;
  align-items: center;

  background-color: #18454a;

  font-family: 'Roboto', sans-serif;
  font-size: 36px;
  font-weight: bold;
  
  caret-color: transparent;

  transition: .4s;
}

/*Message*/
.message {
  height: 10%;
  max-width: 600px;

  margin: 0 auto;

  display: flex;
  justify-content: center;
  align-items: center; 
  
  font-family: 'Roboto', sans-serif;
  font-size: 26px;
  text-align: center;
  font-weight: bold;
  color: rgb(196, 60, 60);

  transition: all .7s;
}

/*Restart button*/
.restart {
  height: 9.5%; 

  margin: 0 auto;
  padding: 0;

  font-size: 30px;

  transition: all .2s;
}


.restart--rotate {
  width: 36px;
  transform: rotate(-75deg);
  font-size: 36px;
  transition: all .2s;
}

.restart:hover {
  color: #46d65e;
  transition: all .2s;
  transform: rotate(-180deg);
}


/*Keyboard.js*/

.keyboard-container {
  max-width: 900px;
  height: 20%;
 
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  flex-basis: 100%;

  position: relative;

  color: white;
}

button[class*=letter] {
  width: 40px;
  height: 40px;

  font-family: 'Roboto', sans-serif;
  font-weight: 900;
  font-size: 18px;

  text-align: center;
  vertical-align: middle;
  line-height: 35px;

  border: 2px solid #18454a;
  border-radius: 5px;
}

@media screen and (max-width: 420px) {
  button[class*=letter] {
    width: 45px;
    
    font-size: 16px;
  }
}

@media screen and (max-width: 360px) {
  button[class*=letter] {
    font-size: 12px;
    }
}

button[class*=letter]:hover {
  background-color: #18454a;
  border: 2px solid #18454a;
  cursor: pointer;
  transition: all .15s;
  caret-color: transparent;
}

.delete {
  width: 60px;
  height: 40px;

  text-align: center;
  vertical-align: middle;

  font-family: 'Roboto', sans-serif;
  line-height: 35px;
  font-weight: bold;

  border: 2px solid rgb(196, 58, 58);
  border-radius: 5px;

  transition: all .15s;
  
  cursor: pointer;
}


@media screen and (max-width: 768px) {
  .delete {
    position: absolute;
    left: 1%;
    top: -45%;
    width: 20% !important;
  }
}

@media screen and (max-width: 320px) {
  .delete {
    font-size: 12px;
  }
}

.delete:hover {
  background-color: rgb(196, 58, 58);
}

.enter {
  width: 50px;
  height: 40px;

  border: 2px solid green;
  border-radius: 5px;

  font-family: 'Roboto', sans-serif;
  text-align: center;
  vertical-align: middle;
  line-height: 35px;
  font-weight: bold;

  transition: all .15s;
  cursor: pointer;
}


@media screen and (max-width: 768px) {
  .enter {
    width: 20% !important;

    position: absolute;
    top: -45%;
    right: 1%;
  }
}

@media screen and (max-width: 320px) {
  .enter {
    font-size: 12px;
  }
}

.enter:hover {
  background-color: green;
}

.QP-letters-content, .AL-letters-content, .ZM-letters-content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  flex-basis: 10%;
  
  margin-bottom: 10px;

  vertical-align: middle;
  text-align: center;
}

.QP-letters-content {
  width: 100%;
}
.AL-letters-content {
  width: 100%;
}

.ZM-letters-content {
  width: 100%;
}

.QP-letters-content button {
  width: 8%;
}

.ZM-letters-content button {
  width: 8%;
}

.AL-letters-content button {
  width: 8%;
}

  /*Стилизация состояние клавиатуры*/


.yellowKeyboard {
  
  color: #c9b458;
  border-color: #c9b458 !important;
}

.yellowKeyboard:hover {
  background-color: #c9b458 !important;
  color: rgb(255, 255, 255);
}

.grayKeyboard {
  color: gray;
  border-color: gray !important;
}
.grayKeyboard:hover {
  color: rgb(255, 255, 255);
  background-color: gray !important;
}


.greenKeyboard {
  
  color: green;
  border-color: green !important;
}
.greenKeyboard:hover {
  color: white;
  background-color: green !important;
}



/*Стилизация для клавиатуры и таблицы*/


.green {
  color: rgb(17, 189, 60);
  
}
.yellow {
  color: rgb(201 180 88);
 
}

.gray {
  color: rgb(126, 117, 117);
  
}

.win {
  color: rgb(23, 158, 23);
  
  
}



.anima {
  transform: rotate(360deg);
  border: 1px solid #b8d3e7;
  box-sizing: border-box;
}






